.g-features{
  padding: var(--padding-section) 0rem 0rem 0rem;
  /*max-width: 100%;*/
  &__item{
    /*max*/
    display: flex;
    justify-content: space-between;
    flex-flow: row nowrap;
    align-items: center;
    padding: var(--padding-section);
    &__description{
      width: 40rem;
      padding: 0 4rem 0 0;
    }
    &__image{
      max-width: 800px;
      width: 95%;
      &--small{
        padding:0 3rem;
      }
    }
    &--flow-right{
      padding-right:0;
    }
    &--flow-left{
      padding-left:0;
      .g-features__item__description{
        padding: 0 0 0 4rem;
      }
    }
    &--dark{
      background-color: var(--color-darkgray);
      color: white;
    }
  }
}

@media only screen and (max-width: 900px) {}
